<template>
  <div>
    <!--  查看调仓移库单 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">调仓移库</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看调仓移库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="调出组织：">
          <p style="width:210px">{{params.company_name}}</p>
        </el-form-item>
        <el-form-item label="单据号：">
          <p style="width:210px">{{params.transfer_sn}}</p>
        </el-form-item>
        <el-form-item label="业务日期：">
          <p style="width:210px">{{params.business_time}}</p>
        </el-form-item>
        <el-form-item label="调出部门：">
          <p style="width:210px">{{params.department_name}}</p>
        </el-form-item>
        <el-form-item label="调入组织：">
          <p style="width:210px">{{params.new_company_name}}</p>
        </el-form-item>
        <el-form-item label="调入仓库：">
          <p style="width:210px">{{params.warehouse_name}}</p>
        </el-form-item>
        <el-form-item label="调入货位：">
          <p style="width:210px">{{params.allocation_name}}</p>
        </el-form-item>
        <el-form-item label="审核状态：">
          <p style="width:210px">{{params.auditor_status_name}}</p>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <vxe-grid border resizable size="mini" show-overflow='tooltip' :columns="tableColumn" :data="tableData"></vxe-grid>
    </div>
    <div>

      <el-form :inline="true" :model="params" class="demo-form-inline">
        <div>
          <el-form-item label="备注：">
            <p>{{params.remark}}</p>
          </el-form-item>
        </div>
        <el-form-item label="创建人：">
          <p>{{params.add_user_name}}</p>
        </el-form-item>
        <el-form-item label="创建时间：">
          <p>{{params.add_time}}</p>
        </el-form-item>
        <el-form-item label="修改人：">
          <p>{{params.update_user_name}}</p>
        </el-form-item>
        <el-form-item label="修改时间：">
          <p>{{params.update_time}}</p>
        </el-form-item>
        <el-form-item label="审核人：">
          <p>{{params.auditor_name}}</p>
        </el-form-item>
        <el-form-item label="审核时间：">
          <p>{{params.auditor_time}}</p>
        </el-form-item>
      </el-form>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!-- 未审核 -->
      <template v-if="params.auditor_status == 1">
        <el-button type="primary" @click="audit('transferWarehouseAuidtorPass','S')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('transferWarehouseAuditorInvalid','F')" v-if="authority.b_invalid==1">作废</el-button>
      </template>

      <template v-if="transferwareSeeGet.after_auditor_print_status == 1">
        <el-button @click="printF" type="primary" v-if="authority.b_print==1 && params.auditor_status == 2">打印</el-button>
      </template>
      <template v-else>
        <el-button @click="printF" type="primary" v-if="authority.b_print==1">打印</el-button>
      </template>

      <template v-if="params.auditor_status == 2">
        <el-button plain @click="AUDIT_GET" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
      </template>

      <!-- 作废 -->
      <template v-if="params.auditor_status == 2 || params.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
    <publicPrint ref="printDelieveryBill" @Search="onSearch" title="warehouseTransfer" returnParams='info_data' returnTableData='list'></publicPrint>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import publicPrint from "common/components/public/PublicPrint.vue"; //公共打印
import { repertory } from "common/utils/public_function/commonUtils";
import { transferware } from "./config/transferwareSee.js";

export default {
  name: "instorageTransferwareSee",
  mixins: [transferware],
  data() {
    return {
      authority: {},
      hasAuditorStatusPrint: null,
      params: {}
    };
  },
  components: {
    publicPrint
  },
  computed: {
    ...mapGetters(["transferwareIndexGet", "transferwareSeeGet"])
  },
  methods: {
    ...mapActions(["transferwareIndexACT"]),
    AUDIT_GET() {
      this.$axMethod
        .v2unAuditorTransfer({
          transfer_id: this.params.transfer_id
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg,
              type: "success"
            });
            this.goBack();
          }
        });
      //弃审
    },
    goBack() {
      this.$router.push({
        path: "/inventory/transferware"
      });
      this.transferwareIndexACT({
        goPage: this.transferwareSeeGet.goPage,
        params: this.transferwareSeeGet.params,
        pageSize: this.transferwareSeeGet.pageSize
      });
    },
    printF() {
      this.$nextTick(() => {
        this.$refs.printDelieveryBill.printPdf({
          transfer_id: this.params.transfer_id
        });
      });
    },
    audit(api, type) {
      let vm = this;
      if (api == "transferWarehouseAuidtorPass") {
        // 校验安全库存金额 风控
        this.$axMethod
          .safetyStockCheck({
            bill_logogram: "tcyk",
            order_type: 2,
            bill_id: vm.params.transfer_id
          })
          .then(data => {
            repertory(
              this,
              {
                goods_info: vm.params.transfer_id,
                status: 2,
                type: 5
              },
              function() {
                vm.$axios({
                  method: "post",
                  url: vm.$api[api],
                  data: {
                    transfer_id: vm.params.transfer_id
                  }
                }).then(res => {
                  if (res.data.code == 200) {
                    if (type == "S") {
                      vm.$message({
                        message: res.data.msg,
                        type: "success"
                      });
                    } else {
                      vm.$message({
                        message: res.data.data,
                        type: "success"
                      });
                    }

                    vm.goBack();
                  }
                });
              }
            );
          });
      } else {
        this.$axios({
          method: "post",
          url: this.$api[api],
          data: {
            transfer_id: this.params.transfer_id
          }
        }).then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            this.goBack();
          }
        });
      }
    },
    initData() {
      this.$axMethod.infoTransferBills(this.params).then(({ data }) => {
        if (data.code == "200") {
          this.tableData = data.data.goods_list;
          this.params = data.data.info;
        }
      });
    }
  },
  created() {
    this.params = {
      transfer_id: this.transferwareSeeGet.transfer_id
    };
    this.initData();
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.authority = JSON.parse(localStorage.getItem("authority"));
  }
};
</script>

<style scoped>
.title {
  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}
.table_container {
  margin-bottom: 20px;
}
.el-form-item {
  margin-right: 50px;
}

.tips li {
  float: right;
  line-height: 50px;
  margin-left: 15px;
}
</style>
